import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: r => require.ensure([], () => r(require('@/components/map/map')))
    },
    {
      path: '/login',
      name: 'login',
      component: r => require.ensure([], () => r(require('@/components/public/login')))
    },
    {
      path: '/map',
      name: 'map',
      component: r => require.ensure([], () => r(require('@/components/map/map')))
    },
    {
      path: '/chart',
      name: 'chart',
      component: r => require.ensure([], () => r(require('@/components/chart/chart')))
    },
    {
      path: '/chartSon',
      name: 'chartSon',
      component: r => require.ensure([], () => r(require('../components/chart/chartSon.vue')))
    },
    {
      path: '/creatOder',
      name: 'creatOder',
      component: r => require.ensure([], () => r(require('@/components/chart/creatOder')))
    },
    {
      path: '/adminConfig',
      name: 'AdminConfig',
      component: r => require.ensure([], () => r(require('@/components/admin/adminConfig')))
    },
    {
      path: '/adminDavise',
      name: 'adminDavise',
      component: r => require.ensure([], () => r(require('@/components/admin/davise')))
    },
    {
      path: '/Histroy',
      name: 'Histroy',
      component: r => require.ensure([], () => r(require('@/components/chart/histroy')))
    },
    {
      path: '/adminAddUser',
      name: 'adminAddUser',
      component: r => require.ensure([], () => r(require('@/components/admin/adminAddUser')))
    }
  ]
})



