import md5 from 'js-md5'
var publicLo = {
  sessionStorageSav: (key, vule) => {
    sessionStorage.setItem(key, vule)
  },
  sessionStorageGet: (key) => {
    return sessionStorage.getItem(key) || ''
  },
  // 上传图片
  senImg: (domId, fn) => {
    // 获取文件
    var files = document.getElementById(domId).files[0]
    // 新建FileReader 对象
    var reader = new FileReader();
    // FileReader对象的readAsDataURL获取图片流
    reader.readAsDataURL(files);
    reader.onload = fn;
  },
  // 时间戳转换
  timeCheng: (time) => {
    var date = new Date(time)
    var Y = date.getFullYear() + '-'
    var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
    var D = date.getDate() + ' '
    var h = date.getHours()
    var m = date.getMinutes()
    var s = date.getSeconds()
    var week = date.getDay()
    week = publicLo.wek(week)
    if (m < 10) {
      m = '0' + m.toString()
    }
    if (M < 10) {
      M = '0' + M.toString()
    }
    if (D < 10) {
      D = '0' + D.toString()
    }
    if (h < 10) {
      h = '0' + h.toString()
    }
    h += ':'
    var tim = {
      ymd: Y + M + D,
      hms: h + m,
      times: Y + M + D + h + m,
      week: week,
      md: M + D,
      m: M,
      d: D,
      s: s,
      y: Y
    }
    return tim
  },
  // 日期转换时间戳
  dataChange: function (data) {
    var date, week, nexDate, today, time
    if (!data) {
      date = new Date()
    } else {
      date = new Date(data)
    }
    // 日期
    week = date.getDay()
    // 时间戳
    time = date.getTime()
    //
    today = this.timeCheng(time)
    var ymdTiem = today.times
    var y = today.y
    var m = today.m
    var d = today.d
    today = today.ymd
    nexDate = this.timeCheng(time + 86400000)
    nexDate = nexDate.ymd

    return {today: today, week: week, nexDate: nexDate, time: time, ymdTiem: ymdTiem, y: y, m: m, d: d}
  },
  // 一周时间
  weekDate: function (data, parm, nuber) {
    'use strict'
    if (!nuber) {
      nuber = 7
    }
    var date, week, nexDate, time
    date = new Date(data)
    time = date.getTime()
    var dat = []
    for (let i = 0; i < nuber; i++) {
      var timeDat = 86400000 * i
      if (parm == 'add') {
        nexDate = this.timeCheng(time + timeDat)
        dat.push(nexDate)
      } else {
        nexDate = this.timeCheng(time - timeDat)
        dat.unshift(nexDate)
      }
    }
    return dat
  },
  wek: (a) => {
    'use strict'
    a = parseInt(a)
    var res
    switch (a) {
      case 1:
        res = '星期一'
        break
      case 2:
        res = '星期二'
        break
      case 3:
        res = '星期三'
        break
      case 4:
        res = '星期四'
        break
      case 5:
        res = '星期五'
        break
      case 6:
        res = '星期六'
        break
      case 0:
        res = '星期日'
        break
    }
    return res
  },
  // 日历
  calendars: () => {
    'use strict'
  },
  // 倒计时
  tmieOut (startTime, time, now) {
    'use strict'
    var jinzhi = 60 * 1000
    var time = parseInt(time) * jinzhi
    var now = now
    var timeOutsNow = now - startTime
    timeOutsNow = time - timeOutsNow
    var timeM = parseInt(timeOutsNow / jinzhi)
    var timeS = timeOutsNow % jinzhi
    timeS = parseInt(timeS / 1000)
    return {timeM: timeM, timeS: timeS}
  },
  // 对象排序
  sortData: (arr, key, up) => {
    // arr：对象，  key：排序key，up：false-从小到大，true-从大到小
    arr.sort(publicLo.sortDataParm(key, up))
    return arr
  },
  // 排序函数
  sortDataParm: (key, disc) => {
    return function (a, b) {
      return disc ? a[key] < b[key] : a[key] > b[key]
    }
  },
  // 组装 post parm
  parmToken: (parm) => {
    // 获取当前时间
    var date = new Date()
    var time = date.getTime()
    // 查看有无分页参数
    var page = null, pageSize = null
    console.log(parm)
    if (parm.page) {
      page = parm.page
      pageSize = parm.pageSize
      // 去掉分页参数
      delete parm.page
      delete parm.pageSize
    }
    var DATA = {
      token: sessionStorage.getItem('token') || 'eyJhbGciOiJIUzI1NiJ9.eyJqdGkiOiJjdXN0b21lciIsImlhdCI6MTUxMzA3NDEyMSwic3ViIjoiMCIsImV4cCI6MTUxNDU0NTM1MCwibmJmIjoxNTEzMDc0MTIxfQ.RonwXddXdXeZOp3-KhpwJxsTEOIFX_zigiE5BPGJo7w',
      timestamp: time,
      sign: md5(sessionStorage.getItem('token') + time + parm),
      bizId: 111,
      data: parm || {},
      pager: {
        startNo: page || null,
        endNo: pageSize || null
      }
    }
    return DATA
  }
}

export default {publicLo}



